import NavLink from './NavLink.vue';
export default {
  components: {
    NavLink: NavLink
  },
  computed: {
    data: function data() {
      return this.$page.frontmatter;
    },
    actionLink: function actionLink() {
      return {
        link: this.data.actionLink,
        text: this.data.actionText
      };
    }
  }
};